.Person {
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #eee;
    border-radius: 10px;
    box-shadow: 4px 4px 6px #ccc;
    width: 50%;
    color: chocolate;
    background-color: lightgoldenrodyellow;
}

@media (min-width: 500px)  {
    .Person{
        width: 450px;
    }
}

.red {color: red !important}
.green {color: green}
.bold {font-size: 50px}